.bg {
  background-repeat: no-repeat;
  background-position: top center;
  background-color: #202020;
  background-size: cover;
  position: absolute;
  background-image: url("../img/bgs/specials-2-m.jpg");
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  // height: 100vh;
  -o-transition: opacity 1s ease;
  -ms-transition: opacity 1s ease;
  -moz-transition: opacity 1s ease;
  -webkit-transition: opacity 1s ease;
  transition: opacity 1s ease;

  &.bg-carousel { opacity: 0; }
  &.bg-carousel.m-visible { opacity: 1; }

  // @include breakpoint($tablet) { top: 55px; }

  @include breakpoint($desktop) {
    top: -50px;
    background-image: url("../img/bgs/home-merch-1.jpg");
  }

}

.bg-home-merch-1,
.bg-home-merch-2,
.bg-home-merch-3 { top: -50px; }


.bg-home-merch-1 { background-image: url("../img/bgs/home-merch-1.jpg"); }
.bg-home-merch-2 { background-image: url("../img/bgs/home-merch-2.jpg"); }
.bg-home-merch-3 { background-image: url("../img/bgs/home-merch-3.jpg"); }

.bg-about-videos-1 { background-image: url("../img/bgs/about-1.jpg"); }
.bg-about-videos-2 { background-image: url("../img/bgs/about-2.jpg"); }

.bg-contact-tour-1,
.bg-contact-tour-2,
.bg-contact-tour-3 { top: 0; }

.bg-contact-tour-1 { background-image: url("../img/bgs/contact-tour-1.jpg"); }
.bg-contact-tour-2 { background-image: url("../img/bgs/contact-tour-2.jpg"); }
.bg-contact-tour-3 { background-image: url("../img/bgs/contact-tour-3.jpg"); }

.bg-specials-press-1 { background-image: url("../img/bgs/specials-1.jpg"); }
.bg-specials-press-2 { background-image: url("../img/bgs/specials-4.jpg"); }
.bg-specials-press-3 { background-image: url("../img/bgs/specials-5.jpg"); }


.b-criminals-banner {
  text-align: center;
  align-items: center;
  justify-content: center;
  color: white;
  font-family: "Montserrat",sans-serif;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1.45rem;
  letter-spacing: .7px;
  background-image: url(../img/banner-bg.jpg);
  background-size: cover;
  background-position: center;
  display: flex;
  min-height: 40px;
  width: 100%;
  padding: 1rem;
  transition: all .3s ease-in-out;

  &:hover {
    transform: scale(1.05);

  }

  &.m-top {
    position: absolute;
    top: 0;
  }

  @include breakpoint($tablet) {
    height: 100px;
  }
  @include breakpoint($desktop) {
    height: 60px;
    background-image: url(../img/banner-bg.jpg);
  }
}
